import { Icon } from '@ant-design/compatible'
import { Common, Constants, JsonViewModal, MultiTags, SelectLocation } from '@hosoft/hos-admin-common'
import { ModelService } from '@hosoft/hos-admin-common'
import { Divider, Tag } from 'antd'
import React from 'react'

import renderListItem from '../components/ListItem'
import renderObjectItem from '../components/ObjectItem'
import { SKIP_PROPS } from './constants'

const { API_FIELD_TYPE } = Constants

const getEnumValue = (enumObj, key) => {
    let result = ''
    if (enumObj) {
        if (key instanceof Array) {
            result =  key.map(k => <Tag>{enumObj[k] || k}</Tag>)
        } else {
            result = enumObj[key] || key || '(空)'
        }
    } else {
        result = key instanceof Array ? key.map(k => <Tag>{k}</Tag>) : (key || '(空)')
    }

    return result
}

const mapValueEnum = enumObj => {
    const valueEnum = []
    for (const key in enumObj) {
        if (enumObj.hasOwnProperty(key)) {
            valueEnum.push({
                text: enumObj[key],
                value: key
            })
        }
    }

    return valueEnum
}

const getLocationValue = (relations, key) => {
    <SelectLocation editMode={0} location={key} />
}

/**
 * 列表中的Array、Object属性对象渲染时，parentInstance, modelInstance，relModelInstance 由 render 通过 props 传入
 */
export const mapModelToColumn = (modelMeta, modelPath, customMeta, relField, editMode, options, handleOperate, onSubmit) => {
    console.log(`~~~> mapModelToColumn: ${modelPath}`)

    const tableColumns = []
    const columnsStateMap = {}

    if (!modelMeta) {
        console.log(`~~~> mapModelToColumn, modelMeta empty: ${modelPath}`)
        return { tableColumns, columnState: columnsStateMap }
    }

    if (!modelMeta.row_key) {
        modelMeta.row_key = ModelService.getModelTableRowKey(modelMeta)
    }

    if (modelMeta.properties && modelMeta.properties.length > 0) {
        for (let i=0; i<modelMeta.properties.length; i++) {
            const prop = modelMeta.properties[i]
            if (SKIP_PROPS.includes(prop.name) && !(relField && SKIP_PROPS.includes(relField))) {
                continue
            }

            if (prop.output_flag_mod === 0 || prop.output_flag_mod === 2) {
                continue
            }

            const column = {
                valueType: prop.prop_type === 'char' ? 'text' : prop.prop_type,
                searchFlag: 0,
                title: prop.dis_name,
                dataIndex: prop.name,
                ellipsis: true,
                width: 100
            }

            let enumObj
            let RelObjectComp

            const subModelPath = modelPath ? `${modelPath}.${prop.name}` : prop.name

            // 关联字段
            if (prop.relations && prop.relations.rel_type) {
                const isArray = prop.prop_type.indexOf('array') > -1

                prop.relations.rel_type /= 1
                switch (prop.relations.rel_type) {
                case 1: // model
                    if (isArray) {
                        RelObjectComp = renderListItem(null, null, null, prop, subModelPath, customMeta, true, 0, onSubmit)
                    } else {
                        RelObjectComp = renderObjectItem(null, null, null, prop, subModelPath, customMeta, 0, onSubmit)
                    }
                    column.render = (text, record) => {
                        return <RelObjectComp parentInstance={record} modelInstance={record[`${prop.name}`]} relModelInstance={record[`${prop.name}_rel`]} />
                    }
                    break

                case 2: // enum, 有2种，1种是常量中的，另一种是name直接写json键值对
                    try {
                        enumObj = eval(`(${prop.relations.name})`)
                        column.searchFlag = prop.search_flag
                        column.filters = mapValueEnum(enumObj)
                        column.render = (text, record) => {
                            return getEnumValue(enumObj, record[prop.name], isArray)
                        }
                    } catch (e) {
                        console.error('~~~> mapModelToColumn, parse enum error', prop.relations.name)
                    }

                    break
                case 3: // dictionary
                    enumObj = prop.relations.rel_model
                    column.searchFlag = prop.search_flag
                    column.filters = mapValueEnum(enumObj)
                    column.render = (text, record) => {
                        return getEnumValue(enumObj, record[prop.name])
                    }
                    break

                case 4: // location 地域
                    column.sorter = true
                    column.render = (text, record) => {
                        return getLocationValue(prop.relations, record[prop.name], editMode)
                    }
                    break

                default:
                    break
                }
            } else {
                switch (prop.prop_type) {
                case API_FIELD_TYPE.boolean:
                    column.searchFlag = prop.search_flag
                    column.render = text => {
                        return String(text) === 'true' ? '是' : '否'
                    }
                    break
                case API_FIELD_TYPE.date:
                    column.searchFlag = prop.search_flag
                    column.valueType = 'dateTime'
                    column.sorter = true
                    break
                case API_FIELD_TYPE.objectId:
                    column.searchFlag = 1
                    break
                case API_FIELD_TYPE.char:
                    column.sorter = !!(prop.index || prop.unique)
                    column.searchFlag = prop.search_flag
                    break
                case API_FIELD_TYPE.number:
                    column.sorter = true
                    column.searchFlag = prop.search_flag
                    break
                case API_FIELD_TYPE['array-of-boolean']:
                case API_FIELD_TYPE['array-of-char']:
                case API_FIELD_TYPE['array-of-number']:
                case API_FIELD_TYPE["array-of-objectId"]:
                    column.searchFlag = prop.search_flag
                    column.render = (text, record) => {
                        const val = record ? record[prop.name] : ''
                        // Table list editMode is always view only
                        return <MultiTags tags={(val instanceof Array) ? val : [val]} editMode={0} />
                    }
                    break
                case API_FIELD_TYPE['array-of-object']:
                case API_FIELD_TYPE.array:
                    if (prop.properties && prop.properties.length > 0) {
                        const ListComp = renderListItem(null, null, null, prop, subModelPath, customMeta, true, 0, onSubmit)
                        column.ellipsis = false
                        column.render = (text, record) => {
                            return <ListComp parentInstance={record} modelInstance={record[prop.name]} relModelInstance={null} />
                        }
                    } else {
                        column.render = (text, record) => {
                            const val = record ? record[prop.name] : ''
                            return <MultiTags tags={(val instanceof Array) ? val : [val]} editMode={0} />
                        }
                    }
                    break
                case API_FIELD_TYPE.object:
                case API_FIELD_TYPE.mix:
                default:
                    if (prop.properties && prop.properties.length > 0) {
                        const ObjectComp = renderObjectItem(null, null, null, prop, subModelPath, customMeta, 0, onSubmit)
                        column.ellipsis = false
                        column.render = (text, record) => {
                            return <ObjectComp parentInstance={record} modelInstance={record[prop.name]} relModelInstance={null} />
                        }
                    }
                    break
                }
            }

            // if (prop.output_flag === 2) {
            //     columnsStateMap[prop.name] = {show: false}
            //     column.hideInTable = true
            // }

            tableColumns.push(column)
        }

        // relField 放到第一列
        if (relField) {
            const relIndex = tableColumns.findIndex(c => c.dataIndex === relField)
            if (relIndex > 0) {
                const relColumn = tableColumns[relIndex]
                tableColumns.splice(relIndex, 1)
                tableColumns.splice(0, 0, relColumn)
            }
        }

        const lastField = modelMeta.properties[modelMeta.properties.length - 1].name
        tableColumns.push({
            title: '操作',
            dataIndex: lastField,
            valueType: 'operation',
            align: 'center',
            fixed: 'right',
            width: 100,
            ellipsis: false,
            render: (text, record) => (
                <>
                    {options.viewJson !== false ? (
                        <>
                            <JsonViewModal title={`${modelMeta.dis_name} JSON`} data={record} />
                            {editMode && (options.edit !== false || options.delete !== false || this.extraOperationRender) ? <Divider type="vertical" /> : null}
                        </>
                    ) : null}
                    {editMode && options.edit !== false ? (
                        <a title="编辑" disabled={record.editable === false} onClick={() => handleOperate && handleOperate('edit', {record})}>
                            <Icon type="edit" />
                            {editMode && (options.delete !== false || this.extraOperationRender) ? <Divider type="vertical" /> : null}
                        </a>
                    ) : null}
                    {editMode && options.delete !== false ? (
                        <a title="删除" disabled={record.editable === false || record.deletable === false} onClick={() => handleOperate && handleOperate('delete', {record})}>
                            <Icon type="delete" />
                            {editMode && (options.extraOperationRender) ? <Divider type="vertical" /> : null}
                        </a>
                    ) : null}
                    {options.extraOperationRender ? options.extraOperationRender(record) : null}
                </>
            ),
        })
    }

    let firstColumn = null
    const index = tableColumns.findIndex(c => c.dataIndex === modelMeta.row_key)
    if (index > -1 && index > 0) {
        firstColumn = tableColumns[index]
        tableColumns.splice(index, 1)
        tableColumns.splice(0, 0, firstColumn)
    }

    if (!firstColumn) {
        firstColumn = tableColumns[0]
    }

    if (firstColumn) {
        const renderFunc = firstColumn.render
        firstColumn.render = (text, record) => {
            return (
                <>
                    <span
                        title="查看详情"
                        style={{cursor: 'pointer'}}
                        onClick={() => handleOperate && handleOperate('view', {record})}
                    >
                        <Icon type="zoom-in" style={{color: '#1890ff'}}/>
                    </span>{' '}
                    {renderFunc ? renderFunc(text, record) : text}
                </>
            )
        }
    }

    if (options.onGetTableColumn) {
        options.onGetTableColumn(tableColumns)
    }

    // console.log(`==> mapModelToColumn result: ${modelPath}`, JSON.stringify(tableColumns), columnsStateMap)

    return {
        tableColumns: setDefaultColumn(tableColumns),
        columnState: columnsStateMap
    }
}
