import '@ant-design/compatible/assets/index.css'

import { DownOutlined, PlusOutlined } from '@ant-design/icons'
import { PageHeaderWrapper } from '@ant-design/pro-layout'
import { Common, ProTable } from '@hosoft/hos-admin-common'
import { ModelService } from '@hosoft/hos-admin-common'
import { Badge, Button, Dropdown, Menu, message, Modal } from 'antd'
import React, { Component } from 'react'

import ObjectEditForm from './components/ObjectEditForm'
import { mapModelToColumn } from './utils/table-column-converter'

class ModelManageLayout extends Component {
    constructor() {
        super()

        this.options = {}
        this.actionRef = React.createRef()
        this.state = {
            editMode: 1,
            tableColumns: null,
            columnState: null,
            editModalVisible: false,
            selectedRowData: null
        }
    }

    async componentDidMount() {
        let {modelName} = this.props
        if (!modelName) {
            const {path} = this.props.match
            modelName = path.substr(path.lastIndexOf('/') + 1)
        }

        if (!modelName) {
            return console.error('modelName must set!')
        }

        console.log(`ModelManageLayout mounted, modelName: ${modelName}`)

        const modelMeta = await ModelService.getModelMeta(modelName)
        const customMeta = this.getCustomModelMeta()
        const {tableColumns, columnState} = mapModelToColumn(
            modelMeta,
            '', // modelPath
            customMeta,
            '', // relField
            2,
            this.options,
            this.handleOperate,
            this.handleSubmit
            // (prop, data, relData) => {
            //     console.log('Model create or updated: ', prop, data, relData)
            //
            //     this.mapModelInst[prop] = data
            //     this.mapModelInst[`${prop}_rel`] = relData
            // }
        )

        const { dispatch } = this.props
        dispatch({
            type: 'plugin/registerPlugin',
            payload: { name: 'auto-manager-page' }
        })

        this.setState({ modelMeta, customMeta, tableColumns, columnState })
    }

    getModelMeta = () => {
        return this.state.modelMeta
    }

    // 如果需要由派生类实现
    getCustomModelMeta = () => {
        return {}
    }

    loadData = params => {
        return ModelService.getModelDataListTable(this.state.modelMeta.name, params)
    }

    reloadData = () => {
        this.actionRef.current.reload()
    }

    showForm = editMode => {
        this.setState({ editMode }, () => {
            this.handleModalVisible(true)
        })
    }

    handleModalVisible = visible => {
        this.setState({ editModalVisible: visible })
    }

    handleSubmit = async (parentInstance, modelName, existInstance, newInstance, relInstance) => {
        console.log('ModelManageLayout handleSubmit: ', parentInstance, modelName, existInstance, newInstance, relInstance)
        const hide = message.loading('正在保存请稍候')

        let result

        try {
            if (existInstance) {
                const id = existInstance[this.state.modelMeta.row_key]
                if (!id) {
                    hide()
                    return message.error(`id 字段丢失: ${this.state.modelMeta.row_key}`)
                }

                result = await ModelService.updateModelData(this.state.modelMeta, id, newInstance)
                hide()
                result && message.success('更新成功')
            } else {
                result = await ModelService.createModelData(this.state.modelMeta, newInstance)
                hide()
            }
        } catch (error) {
            hide()
            message.error(`操作失败: ${error.message}` || '原因未知')
        }

        this.handleModalVisible(false)

        if (this.actionRef) {
            this.actionRef.current.reload()
        }
    }

    handleRemove = async selectedRows => {
        Modal.confirm({'title': '确认', content: '确认删除？', onOk: async () => {
            if (selectedRows instanceof Array) {
                return this.handleBatchRemove(selectedRows)
            }

            const id = selectedRows[this.state.modelMeta.row_key]
            if (!id) {
                return message.error(`id 字段丢失: ${this.state.modelMeta.row_key}`)
            }

            const result = await ModelService.deleteModelData(this.state.modelMeta, id)
            result && message.success('删除成功')

            if (this.actionRef) {
                this.actionRef.current.reload()
            }
        }})
    }

    handleBatchRemove = async selectedRows => {
        const ids = selectedRows.map(r => r[this.state.modelMeta.row_key]).filter(id => !!id)
        if (ids.length === 0) {
            return message.error('请选择记录')
        }

        const result = await ModelService.batchDeleteModelData(this.state.modelMeta, ids)
        result && message.success('删除成功')

        if (this.actionRef) {
            this.actionRef.current.reload()
        }
    }

    handleOperate = async (action, args) => {
        this.setState({ selectedRowData: args.record })
        if (action === 'view') {
            this.setState({editMode: 0, selectedRowData: args.record}, () => {
                this.handleModalVisible(true)
            })
        } else if (action === 'edit') {
            this.setState({editMode: 2, selectedRowData: args.record}, () => {
                this.showForm(2)
            })
        } else if (action === 'delete') {
            this.handleRemove(args.record)
        }
    }

    render() {
        const {modelMeta, customMeta, tableColumns, columnState, selectedRowData, editMode} = this.state
        const {editModalVisible} = this.state
        const {options} = this
        if (!(modelMeta && tableColumns)) {
            return null
        }

        const canBatchDelete = options.delete !== false && options.batch_delete !== false
        return (
            <PageHeaderWrapper content={this.state.pageHeaderContent}>
                <ProTable
                    headerTitle={`${modelMeta.dis_name}列表`}
                    subTitle={this.subTitle}
                    actionRef={this.actionRef}
                    rowKey={modelMeta.row_key}
                    options={options}
                    toolBarRender={({ selectedRows }) => {
                        if (options.toolBarRender) {
                            return options.toolBarRender(selectedRows)
                        }

                        const count = selectedRows ? selectedRows.length : 0
                        const toolbarButtons = [
                            (options.batchOperations || canBatchDelete) && count > 0 ? (
                                <Dropdown
                                    key="batchMenu"
                                    overlay={
                                        <Menu onClick={e => {
                                            if (selectedRows.length === 0) {
                                                return message.info('请先选择记录')
                                            }

                                            this.handleOperate(e.key, {record: selectedRows})
                                        }}>
                                            {canBatchDelete ? <Menu.Item key="delete">删除</Menu.Item> : null}
                                            {options.batchOperations ? (options.batchOperations instanceof Array ?
                                                options.batchOperations.map(operation => (operation)) : options.batchOperations)
                                                : null
                                            }
                                        </Menu>
                                    }
                                >
                                    <Button>
                                        <Badge offset={[-6, -4]} count={count} />
                                        <span>批量操作</span>
                                        <DownOutlined />
                                    </Button>
                                </Dropdown>
                            ) : null,
                            options.create !== false ? (
                                <Button key="new" icon={<PlusOutlined />} type="primary" onClick={() => {
                                    this.showForm(1)
                                }}>
                                    新建
                                </Button>
                            ) : null
                        ]

                        Common.pluginManager().onRenderHook('ProTableToolbar', this, selectedRows, options, toolbarButtons)
                        return toolbarButtons
                    }}
                    tableAlertRender={selectedRowKeys => (
                        <div>已选择 <a style={{ fontWeight: 600 }}>{selectedRowKeys.length}</a> 项</div>
                    )}
                    request={params => this.loadData(params)}
                    columns={tableColumns}
                    columnState={columnState}
                    rowSelection={options.rowSelection !== undefined ? options.rowSelection : {}}
                />

                {editModalVisible ? (
                    <ObjectEditForm
                        editMode={editMode}
                        modelMeta={modelMeta}
                        modelInstance={selectedRowData}
                        customMeta={customMeta}
                        onSubmit={async (parentInstance, modelName, existInstance, newInstance, relInstance) => {
                            this.handleSubmit(parentInstance, modelName, existInstance, newInstance, relInstance)
                        }}
                        onCancel={() => this.handleModalVisible(false)}
                        modalVisible
                    />
                ) : null}
                {this.renderExtra ? this.renderExtra() : null}
            </PageHeaderWrapper>
        )
    }
}

export default ModelManageLayout
