import { Icon } from "@ant-design/compatible"
import { Common, Constants, JsonViewModal, MultiTags, RangePicker, SelectLocation } from '@hosoft/hos-admin-common'

import _ from 'lodash'
import moment from 'moment'
import React from 'react'

import renderListItem from '../components/ListItem'
import renderObjectItem from '../components/ObjectItem'
import { SKIP_PROPS } from './constants'

const { API_FIELD_TYPE } = Constants
const rHttp = new RegExp(/https?:\/\//i)

const renderDateTimeItem = (editMode, fieldName, value, onChange) => {
    return editMode ? props => (
        <RangePicker selectRange={false} onChange={newVal => {
            props.form && props.form.setFieldsValue({[fieldName]: value})
            onChange && onChange(newVal)
        }} />
    ) : value ? moment(value).format('YYYY/MM/DD HH:mm:ss') : ''
}

const renderLocationItem = (editMode, fieldName, value, onChange) => {
    return props => (
        <SelectLocation editMode={editMode} location={value} onChange={newVal => {
            props.form && props.form.setFieldsValue({[fieldName]: newVal})
            onChange && onChange(newVal)
        }} />
    )
}

const renderMultiTagItem = (editMode, fieldName, value, onChange) => {
    return props => (
        <MultiTags tags={value} editMode={editMode} onChange={newVal => {
            props.form && props.form.setFieldsValue({[fieldName]: newVal})
            onChange && onChange(newVal)
        }} />
    )
}

const processFieldUrl = fieldProp => {
    if (typeof fieldProp.initialValue === 'string' && rHttp.test(fieldProp.initialValue)) {
        fieldProp.link = fieldProp.initialValue
        fieldProp.linkTarget = '_blank'
    }
}

// editMode, 0: view, 1: create, 2: update
export const mapModelToForm = (parentInstance, modelInstance, modelMeta, modelPath, customMeta, editMode, onSubmit) => {
    console.log(`---> mapModelToForm: ${modelPath}`, parentInstance, editMode, modelInstance)

    const result = []

    if (modelMeta.properties && modelMeta.properties.length > 0 && modelMeta.editable !== false) {
        for (let i=0; i<modelMeta.properties.length; i++) {
            const prop = modelMeta.properties[i]
            if (SKIP_PROPS.includes(prop.name)) {
                continue
            }

            if (!editMode && (prop.output_flag_mod === 0 || prop.output_flag_mod === 2)) {
                continue
            } else if (editMode && prop.input_flag === 0) {
                continue
            }

            if (editMode === 1 && (prop.auto_increment || prop.name === 'enabled')) {
                continue
            }

            const widgetProp = !editMode ? 'viewWidget' : 'widget'
            const subModelInstance = modelInstance ? modelInstance[prop.name] : null
            const subModelPath = modelPath ? `${modelPath}.${prop.name}` : prop.name
            const relType = _.get(prop, ['relations', 'rel_type']) / 1

            let subRelModelInst
            let fieldProps = null
            let enumObj = null
            let options = null
            let checkUrl = false
            let required = !!prop.require

            // 关联字段
            if (prop.auto_increment || prop.unique) {
                if (editMode === 2) {
                    fieldProps = {viewMode: true}
                    fieldProps[widgetProp] = 'number'
                }
            } else if (relType) {
                const isArray = prop.prop_type.indexOf('array') > -1
                required = required && !isArray

                switch (relType) {
                case 1: // table
                    required = false
                    subRelModelInst = modelInstance ? modelInstance[`${prop.name}_rel`] : null
                    fieldProps = { forwardRef: true }
                    fieldProps[widgetProp] = (isArray || editMode)
                        ? renderListItem(parentInstance, subModelInstance, subRelModelInst, prop, subModelPath, customMeta, isArray, editMode, onSubmit)
                        : renderObjectItem(parentInstance, subModelInstance, subRelModelInst, prop, subModelPath, customMeta, editMode, onSubmit)
                    break

                case 2: // enum, 有2种，1种是常量中的，另一种是name直接写json键值对
                    try {
                        enumObj = eval(`(${prop.relations.name})`)
                        options = []
                        for (const enumKey in enumObj) {
                            if (enumObj.hasOwnProperty(enumKey)) {
                                options.push({label: enumObj[enumKey], value: enumKey})
                            }
                        }

                        fieldProps = {
                            widget: 'select',
                            options,
                            widgetProps: { placeholder: '请选择' },
                        }
                    } catch (e) {
                        console.error(e, prop.relations.name)
                    }

                    break
                case 3: // dictionary
                    enumObj = prop.relations.rel_model
                    if (enumObj) {
                        options = []
                        for (const enumKey in enumObj) {
                            if (enumObj.hasOwnProperty(enumKey)) {
                                options.push({label: enumObj[enumKey], value: enumKey})
                            }
                        }

                        fieldProps = {
                            widget: 'select',
                            options,
                            widgetProps: { placeholder: '请选择' },
                        }
                    }
                    break

                case 4: // location 地域
                    fieldProps = {
                        [widgetProp]: renderLocationItem(editMode, prop.name, subModelInstance, val => {
                            console.log(`---> mapModelToForm renderLocationItem, ${prop.name} value changed: `, val)
                        }),
                        options: [],
                        widgetProps: { placeholder: '请选择' },
                    }
                    break

                default:
                    break
                }
            } else {
                switch (prop.prop_type) {
                case API_FIELD_TYPE.boolean:
                    fieldProps = { widget: 'checkbox' }
                    break
                case API_FIELD_TYPE.char:
                case API_FIELD_TYPE.objectId:
                    checkUrl = true
                    fieldProps = {
                        widget: (prop.name === 'description' || prop.width > 1000) ? 'textarea' : 'input',
                        widgetProps: { maxLength: prop.width },
                    }
                    break
                case API_FIELD_TYPE.date:
                    fieldProps = { [widgetProp]: renderDateTimeItem(editMode, prop.name, subModelInstance, val => {
                        console.log(`---> mapModelToForm renderDateTimeItem, ${prop.name} value changed: `, val)
                    }) }
                    break
                case API_FIELD_TYPE.number:
                    fieldProps = {
                        widget: 'number',
                        widgetProps: { maxLength: prop.width },
                    }
                    break
                case API_FIELD_TYPE['array-of-boolean']:
                case API_FIELD_TYPE['array-of-char']:
                case API_FIELD_TYPE['array-of-number']:
                case API_FIELD_TYPE["array-of-objectId"]:
                    fieldProps = {
                        widget: renderMultiTagItem(editMode, prop.name, subModelInstance, val => {
                            console.log(`---> mapModelToForm renderMultiTagItem, ${prop.name} value changed: `, val)
                        })
                    }
                    break
                case API_FIELD_TYPE['array-of-object']:
                case API_FIELD_TYPE.array:
                    required = false
                    if (prop.properties && prop.properties.length > 0) {
                        fieldProps = { forwardRef: true }
                        fieldProps[widgetProp] = renderListItem(
                            parentInstance,
                            subModelInstance,
                            null,
                            prop,
                            subModelPath,
                            customMeta,
                            true,
                            editMode,
                            onSubmit
                        )
                    }
                    break
                case API_FIELD_TYPE.object:
                case API_FIELD_TYPE.mix:
                default:
                    if (prop.properties && prop.properties.length > 0) {
                        required = false
                        fieldProps = { forwardRef: true }
                        fieldProps[widgetProp] = renderObjectItem(
                            parentInstance,
                            subModelInstance,
                            null,
                            prop,
                            subModelPath,
                            customMeta,
                            editMode,
                            onSubmit
                        )
                    } else {
                        checkUrl = true
                        fieldProps = {
                            viewWidget: () => subModelInstance ? (
                                <JsonViewModal title={`查看 ${prop.dis_name}`} data={subModelInstance}>
                                    <Icon type="eye" /> 查看
                                </JsonViewModal>
                            ) : null,
                            widget: 'textarea',
                            widgetProps: { rows: 5, placeholder: 'mix/object 类型如果需要请输入对象JSON', defaultValue: JSON.stringify(subModelInstance || "", null, 2) }
                        }
                    }
                    break
                }
            }

            if (fieldProps) {
                const initValue = subModelInstance || undefined
                const fieldMeta = {
                    key: prop.name, // subModelPath,
                    label: prop.dis_name,
                    required,
                    initialValue: !editMode ? initValue : initValue || prop.default_val,
                    ...fieldProps,
                }

                if (prop.description) {
                    fieldMeta[!editMode ? 'tooltip' : 'extra'] = prop.description
                }

                if (checkUrl) {
                    processFieldUrl(fieldMeta)
                }

                // console.log(fieldMeta)
                result.push(fieldMeta)
            }
        }
    }

    // console.log(`---> mapModelToForm result: ${modelPath}`, result)
    return result
}
