import { Form as LegacyForm } from '@ant-design/compatible'
import { Constants } from "@hosoft/hos-admin-common"
import { Button, message,Modal } from 'antd'
import _ from 'lodash'
import React, { Component } from 'react'

import { FormBuilder } from '@hosoft/hos-admin-common'
import { mapModelToForm } from '../../utils/form-field-converter'

class ObjectEditForm extends Component {
    constructor() {
        super()
        this.modelInstance = null
        this.modelMeta = null
        this.state = {
            modelName: '',
            editMode: 0,
            fieldMeta: null
        }
    }

    componentDidMount() {
        const { parentInstance, modelInstance, relModelInstance, modelMeta, editMode } = this.props
        const modelName = modelMeta.dis_name

        this.parentInstance = parentInstance
        this.modelInstance = modelInstance
        this.modelMeta = modelMeta

        if (modelMeta.relations && modelMeta.relations.rel_type == 1 && modelMeta.relations.rel_model) {
            this.modelMeta = modelMeta.relations.rel_model
            if (relModelInstance) {
                this.modelInstance = relModelInstance
            }
        }

        if (editMode) {
            this.modelInstance = {...modelInstance}
        }

        this.buildForm()
        this.setState({modelName, editMode})
    }

    buildForm = () => {
        const {modelPath, customMeta, editMode} = this.props
        const meta = {
            columns: 1,
            fields: mapModelToForm(
                this.parentInstance,
                this.modelInstance,
                this.modelMeta,
                modelPath,
                customMeta,
                editMode,
                (parPropInst, prop, existRecord, newValues, relData) => {
                    if (!this.modelInstance) {
                        this.modelInstance = {}
                    }

                    this.modelInstance[prop] = newValues
                    if (relData) {
                        this.modelInstance[`${prop}_rel`] = relData
                    }

                    this.buildForm()
                    console.log(`---> ObjectEditForm, handle prop submit [${modelPath}->${prop}]:`, this.modelInstance, parPropInst, prop, relData)
                }),
            viewMode: !editMode,
        }

        this.setState({fieldMeta: meta})
    }

    handleSubmit() {
        this.props.form.validateFields((err, values) => {
            if (err) return

            const modifiedInstance = _.merge(this.modelInstance, values)
            console.log('---> ObjectEditForm handleSubmit, modified modelInstance: ', modifiedInstance)

            const {modelMeta, onSubmit, modelInstance} = this.props
            if (modelMeta && modelMeta.properties) {
                for (let i=0; i<modelMeta.properties.length; i++) {
                    const prop = modelMeta.properties[i]
                    if (prop.require) {
                        if (_.get(prop, ['relations', 'rel_type']) == 1 ||
                            prop.prop_type == Constants.API_FIELD_TYPE.object ||
                            prop.prop_type == Constants.API_FIELD_TYPE.mix) {
                            if (!modifiedInstance[prop.name]) {
                                return message.error(`${prop.dis_name} 不能为空!`)
                            }
                        }

                        if (prop.prop_type.indexOf('array') > -1) {
                            if (!modifiedInstance[prop.name] || modifiedInstance[prop.name].length === 0) {
                                return message.error(`${prop.dis_name} 不能为空!`)
                            }
                        }
                    }
                }
            }

            // form 编辑不需要 relInstance
            onSubmit && onSubmit(this.parentInstance, modelMeta.name, modelInstance, modifiedInstance, null)
        })
    }

    render() {
        if (!this.state.fieldMeta) {
            return null
        }

        const { modalVisible, onCancel, modelPath } = this.props
        const { editMode, modelName, fieldMeta } = this.state

        console.log('>>> ObjectEditForm render: ', modelPath)

        return (
            <Modal
                centered
                destroyOnClose
                width={800}
                bodyStyle={{ maxHeight: 600, overflow: 'scroll', backgroundColor: '#fff' }}
                title={`${!editMode ? '查看' : (editMode === 1 ? '新建' : '编辑')}${modelName}`}
                visible={modalVisible}
                footer={!editMode ? <Button type="primary" onClick={() => onCancel()}>关闭</Button> : undefined}
                onOk={() => this.handleSubmit()}
                onCancel={() => onCancel()}
            >
                <LegacyForm layout="horizontal">
                    <FormBuilder form={this.props.form} meta={fieldMeta} />
                </LegacyForm>
            </Modal>
        )
    }
}

export default LegacyForm.create()(ObjectEditForm)
