/* eslint-disable eqeqeq */
import { Form as LegacyForm } from '@ant-design/compatible'
import { Alert, message, Modal, Switch } from 'antd'
import { DragDropContext, Draggable,Droppable } from "react-beautiful-dnd"
import React, {useImperativeHandle, useRef, useState} from 'react'

import moveIcon from '../assets/move.svg'

const formLayout = {
    labelCol: {
        xs: {span: 24},
        sm: {span: 8},
    },
    wrapperCol: {
        xs: {span: 24},
        sm: {span: 16},
    },
}

const ChooseFields = LegacyForm.create()(props => {
    const {modelMeta} = props
    const {getFieldDecorator} = props.form

    useImperativeHandle(props.actionRef, () => ({
        getValues: () => {
            return props.form.getFieldsValue()
        }
    }))

    const getItemStyle = (isDragging, draggableStyle) => ({
        userSelect: "none",
        margin: 0,
        padding: '8px 8px 8px 0',
        background: isDragging ? "#eee" : "",
        borderBottom: 'solid 1px #eee',
        ...draggableStyle
    });

    const getListStyle = isDraggingOver => ({
        // background: isDraggingOver ? "lightblue" : "lightgrey",
    });

    const onDragEnd = result => {
        if (!result.destination) {

        }

        // const items = reorder(
        //     this.state.items,
        //     result.source.index,
        //     result.destination.index
        // );
        //
        // this.setState({
        //     items
        // });
    }

    return (
        <LegacyForm {...formLayout} labelAlign="left" layout="horizontal">
            <DragDropContext onDragEnd={onDragEnd}>
                <Droppable droppableId="droppable">
                    {(provided, snapshot) => (
                        <div
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            style={getListStyle(snapshot.isDraggingOver)}
                        >
                            {modelMeta.properties.map((prop, index) => {
                                const dragKey = `d${prop.name}`

                                return (
                                    <Draggable key={dragKey} draggableId={dragKey} index={index}>
                                        {(provided, snapshot) => (
                                            <div
                                                ref={provided.innerRef}
                                                {...provided.draggableProps}
                                                {...provided.dragHandleProps}
                                                style={getItemStyle(
                                                    snapshot.isDragging,
                                                    provided.draggableProps.style
                                                )}
                                            >
                                                <LegacyForm.Item key={prop.name} label={<span><img src={moveIcon} /> {prop.dis_name}</span>} style={{marginBottom: 0}}>
                                                    {getFieldDecorator(prop.name)(
                                                        <Switch defaultChecked={prop.output_flag !== 0 && prop.output_flag !== 2} />
                                                    )}
                                                </LegacyForm.Item>
                                            </div>
                                        )}
                                    </Draggable>
                                )
                            })}
                            {provided.placeholder}
                        </div>
                    )}
                </Droppable>
            </DragDropContext>
        </LegacyForm>
    )
})

const SelectFieldsModal = props => {
    const [currentStep, setCurrentStep] = useState(0)

    const scopeRef = useRef()
    const fieldsRef = useRef()
    const options = useRef({})

    const selectExportFields = () => {
        const setting = scopeRef.current.getValues()
        options.current.scope = setting.scope
        options.current.format = setting.format

        if (setting.scope === 2 && !(props.data && props.data.length > 0)) {
            return message.error('尚未选择任何记录！')
        }
        setCurrentStep(currentStep+1)
    }

    const {onCancel, modelMeta, visible} = props

    return (
        <Modal
            centered
            visible={visible}
            destroyOnClose
            width={800}
            bodyStyle={{ height: 500, overflow: 'scroll', backgroundColor: '#fff' }}
            title="列表字段设置"
            onCancel={() => onCancel && onCancel()}
        >
            <div style={{backgroundColor: '#fafafa', padding: 20, minHeight: 400}}>
                <Alert message="拖动条目可调整显示顺序" type="info" />
                <ChooseFields actionRef={fieldsRef} modelMeta={modelMeta} />
            </div>
        </Modal>
    )
}

export default SelectFieldsModal
