import { DownOutlined, PlusOutlined } from '@ant-design/icons'
import { Constants, ProTable } from "@hosoft/hos-admin-common"
import { ModelService } from '@hosoft/hos-admin-common'
import { Badge, Button, Dropdown, Menu, message, Modal, Tabs } from 'antd'
import React, {useRef, useState} from 'react'

import ObjectEditForm from '../ObjectEditForm'

import { mapModelToColumn } from "../../utils/table-column-converter"

const { TabPane } = Tabs

const makeArray = obj => {
    if (!obj) {
        return []
    }

    if (!(obj instanceof Array)) {
        return [obj]
    }

    return obj
}

const ListEditForm = props => {
    const {parentInstance, modelInstance, relModelInstance, modelMeta, modelPath, customMeta} = props
    const {multiSelect, editMode, onSubmit, onCancel, modalVisible} = props

    const [windowSize, setWindowSize] = useState({width: Constants.MODEL_TABLE_WIDTH, height: 600})
    const [createModalVisible, handleCreateModalVisible] = useState(false)
    const [selectedRowData, setSelectedRowData] = useState(null)
    const [activeTabKey, setActiveTabKey] = useState(multiSelect ? 'list' : 'new')
    const [modelInst, setModelInst] = useState(makeArray(props.modelInstance || modelInstance))
    const [relModelInst, setRelModelInst] = useState(makeArray(props.relModelInstance || relModelInstance))

    const actionRef = useRef()
    const actionAddRef = useRef()

    const parentInst = parentInstance
    const modelName = modelMeta.dis_name
    const isArray = modelMeta.prop_type.indexOf('array') > -1

    let mapModelMeta = modelMeta
    let relField = ''
    let relType

    let useRel = false
    if (modelMeta.relations && modelMeta.relations.rel_type == 1 && modelMeta.relations.rel_model) {
        mapModelMeta = modelMeta.relations.rel_model
        relField = modelMeta.relations.field
        relType = modelMeta.relations.rel_type / 1
        useRel = true
    }

    const parseSubPropListData = (params, setRowIndex) => {
        console.log('~~~> ListEditItem parseSubPropListData => ', modelPath, modelInst, relModelInst)

        const data = []
        if (modelMeta.relations && modelMeta.relations.rel_type == 1 && relModelInst) {
            const { field } = modelMeta.relations
            for (let i=0; i<modelInst.length; i++) {
                const row = modelInst[i]
                const relRow = relModelInst.find(r => r[field] == row)
                let newRow
                if (relRow) {
                    newRow = relRow
                } else {
                    newRow = {}
                    newRow[field] = row
                }

                if (setRowIndex) {
                    newRow._index = i + 1
                }
                data.push(newRow)
            }
        } else {
            for (let i=0; i<modelInst.length; i++) {
                data.push(modelInst[i])
            }
        }

        const result = {
            success: true,
            data,
            pagination: {
                total: data.length,
                pageSize: 10,
                current: parseInt(`${params.currentPage}`, 10) || 1
            }
        }

        return result
    }

    // 只有 rel_type == 1 才会执行 addSelectedRows
    const addSelectedRows = async (action, selectedRows) => {
        if (selectedRows.length === 0) {
            return
        }

        if (!multiSelect) {
            const rField = selectedRows[0]
            const fieldVal = rField[modelMeta.relations.field]

            modelInst.splice(0, modelInst.length, fieldVal)
            relModelInst.splice(0, modelInst.length, rField)
        } else {
            for (let i=0; i<selectedRows.length; i++) {
                const rField = selectedRows[i]
                const fieldVal = rField[modelMeta.relations.field]
                modelInst.push(fieldVal)
                relModelInst.push(rField)
            }
        }

        setModelInst(modelInst)
        setRelModelInst(relModelInst)

        await action.reload()
    }

    const handleSubmit = async (parPropInst, prop, existRecord, newValues, relData) => {
        parPropInst[prop] = newValues
        if (relData) {
            parPropInst[`${prop}_rel`] = relData
        }

        console.log('~~~> ListEditItem handleSubmit: ', modelPath, prop, parPropInst)
    }

    const handleOperate = async (action, args) => {
        console.log('~~~> ListEditItem handleOperate: ', modelPath, action, args)

        if (action === 'view') {
            await setSelectedRowData(args.record)
            handleCreateModalVisible(true)
        } else if (action === 'edit') {
            await setSelectedRowData(args.record)
            handleCreateModalVisible(true)
        } else if (action === 'delete') {
            Modal.confirm({
                content: `确认删除所选记录？`,
                onOk: () => {
                    const rowKey = mapModelMeta.row_key
                    const records = args.record.current
                    for (let i = 0; i < records.length; i++) {
                        const record = records[i]
                        const existIndex = modelInst.findIndex(r => r[rowKey] === record[rowKey])
                        if (existIndex > -1) {
                            modelInst.splice(existIndex, 1)
                        }
                    }

                    actionRef.current.reload()
                }
            })
        }
    }

    const renderSelRecordTab = () => {
        return (
            <TabPane
                key="list"
                tab={<span onClick={() => setActiveTabKey('list')}>{multiSelect ? `${modelName}列表` : `已选记录`}</span>}
            >
                <ProTable
                    headerTitle={`${modelMeta.dis_name}列表`}
                    subTitle={options.subTitle}
                    actionRef={actionRef}
                    rowKey={rowKey}
                    onFullScreen={() => {
                        if (windowSize.width === Constants.MODEL_TABLE_WIDTH) {
                            setWindowSize({width: window.innerWidth, height: window.innerHeight-110})
                        } else {
                            setWindowSize({width: Constants.MODEL_TABLE_WIDTH, height: 600})
                        }
                    }}
                    options={options}
                    toolBarRender={({ selectedRows }) => {
                        if (options.toolBarRender) {
                            return options.toolBarRender(selectedRows)
                        }

                        const count = selectedRows ? selectedRows.length : 0
                        return [
                            (options.batchOperations || canBatchDelete) && count > 0 ? (
                                <Dropdown
                                    key="batchMenu"
                                    overlay={
                                        <Menu onClick={e => {
                                            if (selectedRows.length === 0) {
                                                return message.info('请先选择记录')
                                            }

                                            handleOperate(e.key, {record: selectedRows})
                                        }}>
                                            {canBatchDelete ? <Menu.Item key="delete">删除</Menu.Item> : null}
                                            {options.batchOperations ? (options.batchOperations instanceof Array ?
                                                options.batchOperations.map(operation => (operation)) : options.batchOperations)
                                                : null
                                            }
                                        </Menu>
                                    }
                                >
                                    <Button>
                                        <Badge offset={[-6, -4]} count={count} />
                                        <span>批量操作</span>
                                        <DownOutlined />
                                    </Button>
                                </Dropdown>
                            ) : null,
                            options.create !== false ? (
                                <Button key="new" icon={<PlusOutlined />} type="primary" onClick={() => {
                                    handleCreateModalVisible(true)
                                }}>
                                    新建
                                </Button>
                            ) : null
                        ]
                    }}
                    tableAlertRender={selectedRowKeys => (
                        <div>已选择 <a style={{ fontWeight: 600 }}>{selectedRowKeys.length}</a> 项</div>
                    )}
                    request={params => parseSubPropListData(params, rowKey === '_index')}
                    columns={tableColumns}
                    rowSelection={editMode && multiSelect ? options.rowSelection !== undefined ? options.rowSelection : {} : null}
                />
            </TabPane>
        )
    }

    const renderAddRecordTab = () => {
        return (
            <TabPane
                tab={
                    <span onClick={() => setActiveTabKey('new')}>
                        {multiSelect ? <PlusOutlined /> : null}{`${multiSelect ? '添加' : '选择'}${modelMeta.dis_name}`}
                    </span>
                }
                key="new"
            >
                <ProTable
                    actionRef={actionAddRef}
                    rowSelection={multiSelect ? {} : null}
                    rowKey={rowKey}
                    options={{ density: true, fullScreen: false, reload: true, setting: true }}
                    toolBarRender={({ selectedRows }) => {
                        if (selectedRows && selectedRows.length > 0) {
                            return [
                                <Button icon={<PlusOutlined />} type="primary" onClick={async () => {
                                    await addSelectedRows(actionRef.current, selectedRows, modelInst, relModelInst)
                                    setActiveTabKey('list')
                                }}>
                                    添加所选
                                </Button>
                            ]
                        }

                        return []
                    }}
                    tableAlertRender={selectedRowKeys => (
                        <div>
                            已选择 <a style={{ fontWeight: 600 }}>{selectedRowKeys.length}</a> 项
                        </div>
                    )}
                    request={params => ModelService.getModelDataListTable(modelMeta.relations.name, params)}
                    columns={addTableColumns}
                />
            </TabPane>
        )
    }

    const options = customMeta[mapModelMeta.name] || { search: false, create: !useRel }

    let addTableColumns
    if (editMode && relType === 1) {
        addTableColumns = mapModelToColumn(mapModelMeta, modelPath, customMeta, relField, 0, options, handleOperate, handleSubmit).tableColumns
        if (!multiSelect) {
            const operateColumn = addTableColumns[addTableColumns.length - 1]
            operateColumn.width = 50
            operateColumn.render = (text, record) => (
                <Button onClick={() => {
                    const fieldVal = record[modelMeta.relations.field]

                    const retRelModelInst = record
                    const retModelInst = fieldVal

                    console.log('~~~> ListEditItem onSubmit single: ', modelMeta.name, retModelInst, retRelModelInst)
                    onSubmit && onSubmit(parentInst, modelMeta.name, modelInstance, retModelInst, retRelModelInst)
                }}>选择</Button>
            )
        }
    }

    const {tableColumns, /* columnState, */} = mapModelToColumn(mapModelMeta, modelPath, customMeta, relField, editMode, options, handleOperate, handleSubmit)
    const rowKey = mapModelMeta.row_key
    const canBatchDelete = options.delete !== false && options.batch_delete !== false
    // const count = modelInst ? modelInst.length : 0

    console.log('~~~> ListEditForm render: ', modelPath)

    return (
        <Modal
            centered
            destroyOnClose
            width={windowSize.width}
            bodyStyle={{ padding: 0, height: windowSize.height, overflow: 'scroll', backgroundColor: '#fff' }}
            visible={modalVisible}
            footer={!editMode ? (
                <Button type="primary" onClick={() => onCancel && onCancel()}>
                    关闭
                </Button>
            ) : undefined}
            onOk={() => {
                if (!(modelInst && modelInst.length > 0)) {
                    return Modal.info({title: '温馨提示', content: '尚未选择任何记录!'})
                }

                const retModelInst = isArray ? modelInst : modelInst[0]
                const retRelModelInst = isArray ? relModelInst : relModelInst[0]

                console.log('~~~> ListEditItem onSubmit: ', modelPath, modelMeta.name, retModelInst, retRelModelInst)
                onSubmit && onSubmit(parentInst, modelMeta.name, modelInstance, retModelInst, retRelModelInst)
            }}
            onCancel={() => onCancel && onCancel()}
        >
            <Tabs activeKey={activeTabKey} size="large" animated={false} tabBarStyle={{ backgroundColor: '#fff' }}>
                {multiSelect ? renderSelRecordTab() : null}
                {editMode && relType === 1 ? renderAddRecordTab(multiSelect) : null}
            </Tabs>
            {createModalVisible ? (
                <ObjectEditForm
                    modelMeta={mapModelMeta}
                    modelPath={modelPath}
                    modelInstance={selectedRowData}
                    customMeta={customMeta}
                    editMode={relType === 1 ? 0 : editMode}
                    onSubmit={async (parPropInst, prop, existRecord, newValues, relData) => {
                        console.log('~~~> ListEditItem array element create or update: ', modelPath, modelMeta.name, newValues, relData)

                        if (existRecord) {
                            const existIndex = modelInst.findIndex(r => r[rowKey] === existRecord[rowKey])
                            if (existIndex > -1) {
                                modelInst[existIndex] = newValues
                            } else {
                                console.error('~~~> ListEditItem exist item not found: ', existRecord)
                            }
                        } else {
                            modelInst.push(newValues)
                        }

                        await setModelInst([...modelInst])
                        await actionRef.current.reload()

                        handleCreateModalVisible(false)
                    }}
                    onCancel={() => handleCreateModalVisible(false)}
                    modalVisible={createModalVisible}
                />
            ) : (null)}
        </Modal>
    )
}


export default ListEditForm
