import _ from 'lodash'
import React from 'react'

import OptionsModal from "./components/OptionsModal"
import CommonCrud from "./pages/common-crud"

/**
 * plugin for generate model crud page dynamically,
 * or you can also choose to generate static code to do manual customization.
 */
class AutoCrudPage {
    constructor() {
        _.bindAll(this, ['init', 'renderImportExportMenu', 'renderCrudPage'])
    }

    init(pluginManager) {
        pluginManager.setRenderHook('ProTableToolbar', this.renderImportExportMenu)
        pluginManager.setRenderHook('ModelContent', this.renderCrudPage)
    }

    renderImportExportMenu(tableLayout, selectedRows, options, buttons) {
        const modelMeta = tableLayout.getModelMeta()
        if (modelMeta.name !== 'Model') {
            return // this is only for model
        }

        buttons.push((
            <OptionsModal selectedRows={selectedRows} />
        ))
    }

    renderCrudPage(modelContents, modelName) {
        if (modelContents.find(item => item.name === modelName)) {
            return
        }

        modelContents.push({
            name: modelName,
            content: (
                <CommonCrud modelName={modelName} />
            )
        })
    }
}

export default new AutoCrudPage()
