import { Icon } from '@ant-design/compatible'
import {Badge, Button} from 'antd'
import React, {useState} from 'react'

import ListEditForm from '../ListEditForm'

/**
 * 列表数据有 3 种：
 * 1: 属性为数组，关联到另一张表的数据列表，列表项一般为字符串或数字
 * 2: 和 1 相同，但属性不是数组，编辑时需弹出列表选择
 * 3: 属性为对象数组，或字符、数字数组，不关联其他表
 */
export default (parentInstance, modelInstance, relModelInstance, modelMeta, modelPath, customMeta, multiSelect, editMode, onSubmit) => {
    console.log(`~~~> renderListItem: ${ modelPath}`)

    return props => {
        console.log(`~~~> ListItem render [${modelPath}]`)

        const [modalVisible, handleModalVisible] = useState(false)

        const parentInst = parentInstance || props.parentInstance
        const modelInst = modelInstance || props.modelInstance
        const relModelInst = relModelInstance || props.relModelInstance
        const isArray = modelInst instanceof Array

        const count = isArray ? modelInst.length : 0

        return (
            <>
                <Button onClick={() => {
                    handleModalVisible(true)
                }}>
                    {!editMode ? '查看列表' : (multiSelect ? '设置' : '选择')}
                    { isArray ? (
                        <Badge showZero offset={[-4, -4]} count={count} style={{ marginLeft: 8, backgroundColor: '#e6f7ff', color: count > 0 ? '#2db7f5' : '#999' }} />
                    ) : (
                        <Icon type={modelInst ? 'check' : 'question'} />
                    )}
                </Button>
                {
                    modalVisible ? (
                        <ListEditForm
                            parentInstance={parentInst}
                            modelMeta={modelMeta}
                            modelPath={modelPath}
                            modelInstance={modelInst}
                            relModelInstance={relModelInst}
                            customMeta={customMeta}
                            multiSelect={multiSelect}
                            editMode={editMode}
                            onSubmit={async (parPropInst, prop, existRecord, newValues, relData) => {
                                onSubmit && onSubmit(parPropInst, prop, existRecord, newValues, relData)
                                handleModalVisible(false)
                            }}
                            onCancel={() => handleModalVisible(false)}
                            modalVisible
                        />
                    ) : null
                }
            </>
        )
    }
}
