import { Icon } from '@ant-design/compatible'
import { Common, Constants } from "@hosoft/hos-admin-common"
import { Avatar, Button } from 'antd'
import React, { useState } from 'react'

import ObjectEditForm from '../ObjectEditForm'

export default (parentInstance, modelInstance, relModelInstance, modelMeta, modelPath, customMeta, editMode, onSubmit) => {
    console.log(`---> renderObjectItem: ${modelPath}`)

    return props => {
        // console.log(`ObjectItem ==> render [${modelPath}]`)
        const [modalVisible, handleModalVisible] = useState(false)

        const parentInst = parentInstance || props.parentInstance
        const modelInst = modelInstance || props.modelInstance
        const relModelInst = relModelInstance || props.relModelInstance

        const isObject = (modelMeta.prop_type === Constants.API_FIELD_TYPE.object || modelMeta.prop_type === Constants.API_FIELD_TYPE.mix)
        if (modelMeta.relations && modelMeta.relations.rel_type == 1) {
            if (modelInst !== undefined && !isObject && !relModelInst) {
                return modelInst
            }
        }

        if (!modelInst && !editMode) {
            return '(空)'
        }

        let disName = ''
        let isUser = false

        if (!isObject && relModelInst) {
            if (modelMeta.relations && modelMeta.relations.rel_type == 1 && modelMeta.relations.name === 'User') {
                isUser = true
            }

            disName = isUser ? (
                <><Avatar icon={<Icon type='user' />} size="small" src={Common.ossProcessImg(relModelInst.avatar, 50, 0, true)} /> {relModelInst.nick_name}</>
            ) : relModelInst.dis_name || relModelInst.name
        }

        if (!disName) {
            disName = String(modelInst) || '(空)'
        }

        return (
            <>
                {
                    isObject ? (
                        <Button onClick={() => handleModalVisible(true)}>
                            <Icon type={!editMode ? 'eye' : 'edit'} /> {!editMode ? '查看' : '设置'}
                        </Button>
                    ) : (
                        <a onClick={() => handleModalVisible(true)}>
                            {!isUser ? <Icon type={!editMode ? 'zoom-in' : 'edit'} /> : null} {disName}
                        </a>
                    )
                }
                {
                    // 点击按钮后再渲染
                    modalVisible ? (
                        <ObjectEditForm
                            parentInstance={parentInst}
                            modelMeta={modelMeta}
                            modelPath={modelPath}
                            modelInstance={modelInst}
                            relModelInstance={relModelInst}
                            customMeta={customMeta}
                            editMode={editMode}
                            onSubmit={async (parPropInst, prop, existRecord, newValues, relData) => {
                                onSubmit && onSubmit(parPropInst, prop, existRecord, newValues, relData)
                                handleModalVisible(false)
                            }}
                            onCancel={() => handleModalVisible(false)}
                            modalVisible
                        />
                    ) : (null)
                }
            </>
        )
    }
}
